<template>
	<div id="printer-post">
		<public-header title="编辑打印机"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell>
					<div slot="title">是否启用打印机</div>
					<iswitch :value="data.status" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="data.status"></iswitch>
				</van-cell>
				<van-field
						v-model="data.name"
						label="打印机名称"
						placeholder="请填写打印机名称"
						input-align="right"
				/>
			</van-cell-group>
			<van-radio-group class="margin-10-t" v-model="data.type">
				<van-cell title="打印机类型"></van-cell>
				<van-cell title="飞鹅定制打印机" clickable @click="data.type = 'feie'" class="border-0px" label="不支持打印后自动接单">
					<van-radio name="feie" />
				</van-cell>
				<van-cell title="飞鹅新版定制打印机" clickable @click="data.type = 'feie_new'" class="border-0px" label="支持打印后自动接单">
					<van-radio name="feie_new" />
				</van-cell>

				<van-cell title="365 S1定制打印机" clickable @click="data.type = '365'" class="border-0px" label="机器号以kdt1开头的">
					<van-radio name="365" />
				</van-cell>
				<van-cell title="365 S2定制打印机" clickable @click="data.type = '365_s2'" class="border-0px" label="机器号以kdt2开头的">
					<van-radio name="365_s2" />
				</van-cell>
				<van-cell title="喜讯定制打印机" clickable @click="data.type = 'xixun'" class="border-0px" label="支持打印后自动接单">
					<van-radio name="xixun" />
				</van-cell>
				<van-cell title="易联云定制打印机" clickable @click="data.type = 'yilianyun'" class="border-0px" label="不推荐">
					<van-radio name="yilianyun" />
				</van-cell>
				<van-cell title="飞印打印机" clickable @click="data.type = 'feiyin'" class="border-0px" label="不推荐,后期将停止更新">
					<van-radio name="feiyin" />
				</van-cell>
				<van-cell title="AiPrint打印机" clickable @click="data.type = 'AiPrint'" class="border-0px" label="不推荐,后期将停止更新">
					<van-radio name="AiPrint" />
				</van-cell>
				<van-cell title="启云定制打印机" clickable @click="data.type = 'qiyun'" class="border-0px" label="不推荐,后期将停止更新">
					<van-radio name="qiyun" />
				</van-cell>
			</van-radio-group>
			<van-cell-group class="margin-10-t">
				<template v-if="data.type == 'feie_new'">
					<van-field
							v-model="data.member_code"
							label="USER"
							placeholder="请填写飞鹅USER"
							input-align="right"
					/>
					<van-field
							v-model="data.api_key"
							label="UKEY"
							placeholder="请填写飞鹅UKEY"
							input-align="right"
					/>
				</template>
				<van-field
						v-model="data.print_no"
						label="机器号"
						placeholder="请填写机器号"
						input-align="right"
				/>
				<template v-if="data.type !== 'xixun'">
					<van-field
							v-model="data.key"
							label="打印机key"
							placeholder="请填写打印机key"
							input-align="right"
					/>
				</template>
				<template v-if="data.type == 'feiyin' || data.type == 'AiPrint'">
					<van-field
						v-model="data.member_code"
						label="商户编号"
						placeholder="请填写商户编号"
						input-align="right"
					/>
				</template>
				<template v-if="data.type == 'yilianyun' || data.type == 'qiyun'">
					<van-field
						v-model="data.userid"
						label="用户ID"
						placeholder="请填写用户ID"
						input-align="right"
					/>
					<van-field
						v-model="data.api_key"
						label="apikey"
						placeholder="请填写apikey"
						input-align="right"
					/>
				</template>
				<van-field
						v-model="data.print_nums"
						label="打印联数"
						placeholder="默认为1"
						input-align="right"
				/>
				<van-field
						v-model="data.print_header"
						label="头部自定义信息"
						placeholder="建议少于20个字"
						input-align="right"
				/>
				<van-field
						v-model="data.print_footer"
						label="尾部自定义信息"
						placeholder="建议少于20个字"
						input-align="right"
				/>
			</van-cell-group>
			<van-radio-group class="margin-10-t" v-model="data.print_label_type">
				<van-cell title="打印指定标签"></van-cell>
				<van-cell title="打印所有的商品" clickable @click="data.print_label_type = 0" class="border-0px">
					<van-radio :name="0" />
				</van-cell>
				<van-cell title="打印指定标签" clickable @click="data.print_label_type = 1, onPrinterPicker()" class="border-0px">
					<van-radio :name="1" />
				</van-cell>
			</van-radio-group>
			<van-radio-group class="margin-10-t" v-model="data.is_print_all">
				<van-cell title="打印类型"></van-cell>
				<van-cell title="整单打印" clickable @click="data.is_print_all = '1'" class="border-0px" label="打印订单的全部商品条目信息">
					<van-radio name="1" />
				</van-cell>
				<van-cell title="分单打印" clickable @click="data.is_print_all = '0'" class="border-0px" label="订单里的全部商品每个打印一次">
					<van-radio name="0" />
				</van-cell>
			</van-radio-group>
			<template v-if="util.checkplugin('svip')">
				<van-radio-group class="margin-10-t" v-model="data.data.format.goods_title.font_size">
					<van-cell title="商品名称文字大小"></van-cell>
					<van-cell title="大字号" clickable @click="data.data.format.goods_title.font_size = 'large'" class="border-0px">
						<van-radio name="large" />
					</van-cell>
					<van-cell title="正常字号" clickable @click="data.data.format.goods_title.font_size = 'normal'" class="border-0px">
						<van-radio name="normal" />
					</van-cell>
				</van-radio-group>
				<van-radio-group class="margin-10-t" v-model="data.data.format.note.font_size">
					<van-cell title="订单备注文字大小"></van-cell>
					<van-cell title="大字号" clickable @click="data.data.format.note.font_size = 'large'" class="border-0px">
						<van-radio name="large" />
					</van-cell>
					<van-cell title="正常字号" clickable @click="data.data.format.note.font_size = 'normal'" class="border-0px">
						<van-radio name="normal" />
					</van-cell>
				</van-radio-group>
			</template>
			<van-radio-group class="margin-10-t" v-model="data.qrcode_type">
				<van-cell title="二维码类型"></van-cell>
				<van-cell title="配送员接单二维码" clickable @click="data.qrcode_type = 'delivery_assign'" class="border-0px">
					<van-radio name="delivery_assign" />
				</van-cell>
				<van-cell title="自定义二维码链接" clickable @click="data.qrcode_type = 'custom',onQrcodePicker()" class="border-0px">
					<van-radio name="custom" />
				</van-cell>
			</van-radio-group>
			<div class="padding-15">
				<van-button size="normal" block class="bg-info" @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--指定标签选择器-->
		<van-popup v-model="printerPicker" position="bottom">
			<div class="popup-title">
				<span class="c-info" @click="onLabelsPicker">取消</span>
				<span>打印指定标签</span>
				<span class="c-info" @click="onPrinterPicker">确定</span>
			</div>
			<div class="popup-content">
				<van-checkbox-group v-model="data.print_label">
					<van-cell-group>
						<van-cell v-for="(item, index) in print_labels" clickable :key="item.id" :title="item.title" @click="onToggleLabels(index)">
							<van-checkbox :name="item.id" ref="checkboxes" />
						</van-cell>
					</van-cell-group>
				</van-checkbox-group>
			</div>
		</van-popup>
		<!--自定义二维码链接-->
		<van-popup v-model="qrcodePicker" position="bottom">
			<div class="popup-title">
				<span class="c-info" @click="onCloseQrcode">取消</span>
				<span>自定义二维码链接</span>
				<span class="c-info" @click="onQrcodePicker">确定</span>
			</div>
			<div class="popup-content">
				<van-cell-group class="margin-10-t">
					<van-field
							v-model="data.qrcode_link"
							label="自定义二维码链接"
							input-align="right"
					/>
				</van-cell-group>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				id: 0,
				showPreLoading: true,
				printerPicker: false,
				print_labels: [],
				qrcodePicker: false,
				data: {
					status: 0,
					name: '',
					type: 'feie',
					print_no: '',
					key: '',
					api_key: '',
					feie_user: '',
					feie_ukey: '',
					member_code: '',
					userid: '',
					print_nums: '1',
					print_header: '',
					print_footer: '',
					print_label_type: 0,
					print_label: [],
					is_print_all: '',
					qrcode_type: 'delivery_assign',
					qrcode_link: '',
					data: {
						format: {
							goods_title: {
								font_size: 'large',
							},
							note: {
								font_size: 'large',
							}
						}
					}
				}
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					url: 'manage/shop/printer/post',
					data: {
						id: that.id,
						_account_perm: 1,
					},
					success: (res) => {
						this.print_labels = that.util.extend(this.print_labels, res.print_labels);
						if (res.data){
							this.data = that.util.extend(this.data, res.data);
						}
					}
				});
			},
			onPrinterPicker(){
				this.printerPicker = !this.printerPicker;
			},
			onQrcodePicker(){
				this.qrcodePicker = !this.qrcodePicker;
			},
			onCloseQrcode(){
				this.data.qrcode_link = '';
				this.onQrcodePicker();
			},
			onLabelsPicker(){
				this.data.print_labels = '',
				this.onPrinterPicker();
			},
			onToggleLabels(index){
				this.$refs.checkboxes[index].toggle();
			},
			onSubmit(){
				var that = this;
				if (!that.data.name) {
					that.util.$toast('名称不能为空', '', 1000);
					return false;
				}
				if (!that.data.print_no) {
					that.util.$toast('机器号不能为空', '', 1000);
					return false;
				}
				setData({
					vue: that,
					url: 'manage/shop/printer/post',
					data: {
						id: that.id,
						data: that.data
					},
					message: '编辑打印机成功',
					redirect: that.util.getUrl({path: '/pages/printer/list'}),
				});
			},
			jsToggleSwitch(data){
				var that = this;
				that.util.jsToggleSwitch({
					vue: that,
					key: data.keys,
					value: data.value
				});
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#printer-post .content .van-field .van-cell__title{
		max-width: 100px;
	}
	#printer-post .van-popup .van-field .van-cell__title{
		max-width: 120px;
	}
</style>


